body {
  padding: 60px;
  
}

*{
  font-family: Arial, Helvetica, sans-serif
}


input{
  font-size: 1.2rem;
  width: 100%;
  color: grey;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
  width: 30%;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 130px;
}

